.btn_wrap {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    width: 240px;
    height: 72px;
    background-color: #EEEEED;
    border-radius: 80px;
    padding: 0 18px;
    
    will-change: transform;
    transition: all .2s ease-in-out;
}

.btn_wrap:hover {
    /* transition-delay: .4s; */
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}

.socials {
    position: absolute;
    z-index: 99;
    width: 240px;
    height: 72px;
    border-radius: 80px;
    font-family: 'Poiret One', cursive;

    font-size: 20px;
    text-align: center;
    line-height: 70px;
    letter-spacing: 2px;
    color: #EEEEED;
    background-color: #1F1E1E;
    padding: 0 18px;
    transition: all 1.2s ease;
}

.container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 240px;
    height: 64px;
    
    border-radius: 80px;
}
.bi{
    font-size: .8em;
    margin-left: 5px;
}

.container i:nth-of-type(1) {
            transition-delay: 1.1s;
}

.container i:nth-of-type(2) {
            transition-delay: .9s;
}

.container i:nth-of-type(3) {
            transition-delay: .7s;
}

.container i:nth-of-type(4) {
            transition-delay: .4s;
}

.btn_wrap:hover .socials {
    transition-delay: .25s;
    -webkit-transform: translateX(-280px);
            transform: translateX(-280px)
}

.btn_wrap:hover i {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}



@media only screen and (max-width: 1100px){
